import React, {useEffect, useState} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';
import {API_URL, SOCKET_URL} from '../../config';
import {Link} from 'react-router-dom';
import Nav from '../Nav/Nav'
import loading from '../../assets/loading.svg';

export default class Table extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            rows: [],
            loading: false,
            deleting: false
        }
        this.deleteMarket = this.deleteMarket.bind(this);
        this.getData = this.getData.bind(this);
    }

    getData(){
        this.setState({
            loading: true
        })
        axios.get(API_URL + "/api/markets").then((data) => {
            this.setState({
                loading: false,
                rows: data.data
            })
        })
    }

    componentDidMount(){
        this.getData();
    }

    componentWillMount(){
        this.getData()
    }

    deleteMarket(id){
        
        let s = window.confirm("Do you want to delete this?")
        if(s){
            this.setState({
                deleting: true
            })
            axios.delete(`${API_URL}/api/market?id=${id}`).then((data) => {
                this.setState({
                    deleting: false
                })
                this.getData();
            });
        }
    }
    

    render(){
        
        return(
            <React.Fragment>
                <Nav />
                <div className="create" style={{
                    margin: "60px 0"
                }}>
                    {this.state.loading ? 'Loading...' : null}
                    <table className="table border">
                        <thead>
                            <tr>
                            <th scope="col">#</th>
                            <th scope="col">Name</th>
                            <th scope="col">Link</th>
                            <th scope="col">Iframe</th>
                            <th scope="col"><Link to="/create" class="btn btn-primary btn-sm">Add</Link></th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.state.rows.map((row, i) => {
                                    return (
                                    <tr key={i}>
                                        <th scope="row">{i + 1}</th>
                                        <td>{row.name}</td>
                                        <td>{row.link}</td>
                                        <td>{<Link to={`/chart/${row.name}`}>Chart</Link>}</td>
                                    <td><button onClick={() => {this.deleteMarket(row.id)}} class="btn btn-danger btn-sm">{!this.state.deleting ? 'Remove' : <img src={loading} />}</button></td>
                                    </tr>
                                    )
                                
                                })
                            }
                            
                            
                        </tbody>
                    </table>
                </div>
            </React.Fragment>
            )
    }
    
}
