import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';
import {API_URL, SOCKET_URL} from '../../config';
import Nav from '../Nav/Nav'
import loading from '../../assets/loading.svg'

export default class Create extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            link: "",
            loading: false
        }
        this.add = this.add.bind(this);
    }

    add(e){
        e.preventDefault();
        this.setState({
            loading: true
        })
        let data = {
            name: this.state.name,
            link: this.state.link
        }
        axios.post(`${API_URL}/api/market`, data).then((data) => {
            this.setState({
                loading: false
            }, () => {
                alert("Added successfully");
            })
            
        }).catch((e) => {
            this.setState({
                loading: false
            }, () => {
                alert(e)
            })
            
        })
    }

    render(){
        return (
            <div className="container">
                
                <Nav />
                <img src={loading} />
                <div className="row">
                    <div className="col-sm-4 offset-sm-4">
                    <form className="form" style={{"margin": "50px 0"}} onSubmit={this.add}>
                        <div className="form-group">
                            <label>Name</label>
                            <input type="text" className="form-control" name='name' onChange={(e) => {
                                this.setState({
                                    name: e.target.value
                                })
                            }} />
                        </div>
                        <div className="form-group">
                            <label>Link</label>
                            <input type="text" className="form-control" name='link' onChange={(e) => {
                                this.setState({
                                    link: e.target.value
                                })
                            }} />    
                        </div>
                        <div className="form-group text-center">
                            <button type="submit" className="btn btn-primary btn-block">{!this.state.loading ? 'Add' : <img src={loading} />}</button>
                        </div>
                        
                    </form>
                    </div>
                </div>
            </div>
        )
    }
    
}